import React from "react";
import { FuncionarioService } from "prevsystem-service";

const config = require("../../config.json");
const funcionarioService = new FuncionarioService(config);

export default class Home extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            dados: {
                funcionario: "",
            }
        }
        
    }

    componentWillMount() {
        var self = this;
        funcionarioService.BuscarDados()
            .then((result) => {
                self.setState({
                    dados: result.data
                }, () => console.log(this.state));
            });
    }

    render() {
        return (
            <div className="row">
                <div className="col-lg-12">
                    <div className="box">
                        <div className="box-content">
                            <h2>Ol, <span className="text-primary">{this.state.dados.funcionario.NOME_ENTID},</span></h2>
                            <h5>
                                <p className="text-justify">Bem vindo ao Portal do Participante. Aqui, voc encontra as principais informaes sobre seu Plano de Benefcio.
                                Nosso portal est em desenvolvimento, mas diversas funcionalidades j esto disponveis para acesso, dentre elas: </p>
                            </h5>
                            <br/>
                            <div className="list-group">
                                <div className="list-group-item flex-column align-items-start">
                                    <div className="d-flex w-100 justify-content-between">
                                        <h5 className="mb-1"><b>Consulta a Dados Pessoais</b></h5>
                                    </div>
                                    <p className="mb-1">Informaes pessoais fornecidas por meio de sua Ficha de Inscrio na PREVES</p>
                                </div>
                                <div className="list-group-item flex-column align-items-start">
                                    <div className="d-flex w-100 justify-content-between">
                                        <h5 className="mb-1"><b>Planos</b></h5>
                                    </div>
                                    <p className="mb-1">Informaes sobre os planos aos quais voc est inscrito, bem como seu certificado de participao e seu extrato de contribuio</p>
                                </div>
                                <div className="list-group-item flex-column align-items-start">
                                    <div className="d-flex w-100 justify-content-between">
                                        <h5 className="mb-1"><b>Contracheque</b></h5>
                                    </div>
                                    <p className="mb-1">Esta opo mostra o comprovante de pagamento realizado pela PREVES para aquele Participante que j est na condio de Beneficirio</p>
                                </div>
                                <div className="list-group-item flex-column align-items-start">
                                    <div className="d-flex w-100 justify-content-between">
                                        <h5 className="mb-1"><b>Informe de rendimentos</b></h5>
                                        <small className="text-muted"><b>EM DESENVOLVIMENTO</b></small>
                                    </div>
                                    <p className="mb-1">Este item s ter informaes para os participantes que efetuarem contribuies espordicas ou que estiverem na situao de Autopatrocinado. Os demais participantes tero o informe de rendimento disponibilizado por meio de seu rgo de lotao</p>
                                </div>
                                <div className="list-group-item flex-column align-items-start">
                                    <div className="d-flex w-100 justify-content-between">
                                        <h5 className="mb-1"><b>Recadastramento</b></h5>
                                        <small className="text-muted"><b>EM DESENVOLVIMENTO</b></small>
                                    </div>
                                    <p className="mb-1">Este recurso ainda no est disponvel, mas ser utilizado, pelo participante, para alterao de alguns dados pessoais</p>
                                </div>
                                <div className="list-group-item flex-column align-items-start">
                                    <div className="d-flex w-100 justify-content-between">
                                        <h5 className="mb-1"><b>Documentos</b></h5>
                                    </div>
                                    <p className="mb-1">rea destinada a publicaes de documentos diversos destinados ao participante</p>
                                </div>
                                <div className="list-group-item flex-column align-items-start">
                                    <div className="d-flex w-100 justify-content-between">
                                        <h5 className="mb-1"><b>Mensagens</b></h5>
                                    </div>
                                    <p className="mb-1">Informaes enviadas pela PREVES</p>
                                </div>
                                <div className="list-group-item flex-column align-items-start">
                                    <div className="d-flex w-100 justify-content-between">
                                        <h5 className="mb-1"><b>Trocar senha</b></h5>
                                    </div>
                                    <p className="mb-1">O participante tem a possibilidade de alterar a senha de acesso sempre que julgar necessrio. Para isso, dever informar a senha antiga, e a nova senha.</p>
                                </div>
                                <br/>
                                <div className="box-footer">
                                    <h5>
                                        <p className="text-justify">O Portal do Participante foi desenvolvido com o objetivo de facilitar a comunicao entre a PREVES e seus participantes. Caso tenha alguma sugesto ou duvida quanto as informaes
                                        aqui disponibilizadas, entre em contato com a Fundao pelo e-mail <a href="mailto:contato@preves.es.gov.br">contato@preves.es.gov.br</a>.
                                        </p>
                                    </h5>
                                </div>
                                <br/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}



